import React, { useState } from 'react';
import classnames from 'classnames';
import { motion, AnimatePresence } from 'framer-motion';

const links = [{ label: 'More Bells!' }];

export default function Nav(props) {
  const { open, content, onClose } = props;

  return (
    <AnimatePresence>
      {open && (
        <>
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="shade"
            onClick={onClose}
          />
          <motion.nav
            initial={{ transform: 'translate(-100%)' }}
            animate={{ transform: 'translate(0%)' }}
            exit={{ transform: 'translate(-100%' }}
            // layoutTransition
            className={classnames('nav', { open })}
          >
            {content}
          </motion.nav>
        </>
      )}
    </AnimatePresence>
  );
}
